@charset "UTF-8";
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat.woff") format("woff"), url("../fonts/montserrat.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff") format("woff"), url("../fonts/montserrat-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff") format("woff"), url("../fonts/montserrat-bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #2E2E2E;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #F3F4F6;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: 0px 4px 26px 0px rgba(0, 0, 0, 0.0784313725);
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

button, a {
  appearance: none !important;
  color: currentColor;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

li::marker {
  color: transparent;
}

ol:not([class]) {
  list-style-type: decimal;
  list-style-position: inside;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  padding: 0;
  margin: 16px 0 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
@media (min-width: 1440px) {
  ol:not([class]) {
    margin: 24px 0 0;
    gap: 12px;
  }
}
ol:not([class]) li {
  position: relative;
  padding-left: 0;
  margin: 0;
}
ol:not([class]) li a {
  display: inline;
  color: #4176FF;
}
ol:not([class]) li a:hover, ol:not([class]) li a:focus-visible, ol:not([class]) li a:active {
  color: #2E2E2E;
}
ol:not([class]) li::marker {
  color: currentColor;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

section .container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 32px 16px 64px;
}
@media (min-width: 768px) {
  section .container {
    padding: 48px 20px 80px;
  }
}
@media (min-width: 1440px) {
  section .container {
    padding: 48px 100px 80px;
  }
}

header .container,
footer .container,
nav {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 16px;
}
@media (min-width: 768px) {
  header .container,
footer .container,
nav {
    padding: 0 20px;
  }
}
@media (min-width: 1440px) {
  header .container,
footer .container,
nav {
    padding: 0 100px;
  }
}

@media (min-width: 768px) {
  .footer-wrapper nav {
    padding: 0;
  }
}

